.share-modal .header {
    font-size: 16px;
    padding-bottom: 10px;
    border-bottom: 1px solid #e9e9e9;
}

.share-modal .body {
    padding-top: 15px;
    overflow-y: auto;
}

.share-modal .rodal-dialog {
    height: 400px!important;
    width: 600px!important;
}

@media screen and (max-width: 992px) {
    .share-modal .rodal-dialog {
        height: 96%!important;
        width: 90%!important;
    }
}

.share-modal .rodal-cancel-btn,
.share-modal .rodal-confirm-btn {
    position: absolute;
    font: inherit;
    bottom: 16px;
    width: 70px;
    padding: 4px 15px;
    border-radius: 3px;
    transition: background 0.2s;
    border: 1px solid var(--color__100grammes);
}

.share-modal .rodal-confirm-btn {
    color: #fff;
    right: 20px;
    background: var(--color__100grammes);
    cursor: pointer;
    width: 30%;
    border-radius: 45px;
    height: 4rem;
}

.share-modal .rodal-confirm-btn:hover {
    background: var(--color__100grammes);
}

.share-modal .rodal-cancel-btn {
    color: var(--color__100grammes);
    right: 16px;
    background: #fff;
    cursor: pointer;
}

.share-modal .rodal-cancel-btn:hover {
    background: #fafafa;
}

.share-modal .rodal-cancel-btn:focus,
.share-modal .rodal-confirm-btn:focus {
    outline: none;
}

.share-modal .rodal-confirm-btn:active {
    background: var(--color__100grammes);
}

.share-modal .rodal-cancel-btn:active {
    background: #fafafa;
    box-shadow: inset 1px 1px 1px rgba(0, 0, 0, 0.2), 0 0 1px transparent;
}

.share-modal input {
    box-sizing: border-box!important;
    -webkit-box-sizing: border-box!important;
    -moz-box-sizing: border-box!important;
    border: 1px solid #BEBEBE!important;
    padding: 1.5rem!important;
    margin: 0px!important;
    -webkit-transition: all 0.30s ease-in-out!important;
    -moz-transition: all 0.30s ease-in-out!important;
    -ms-transition: all 0.30s ease-in-out!important;
    -o-transition: all 0.30s ease-in-out!important;
    transition: all 0.30s ease-in-out!important;
    outline: none!important;
    width: 100%!important;
    height: 4rem!important;
    border-radius: 45px!important;
}

.share-modal .message {
    box-sizing: border-box!important;
    -webkit-box-sizing: border-box!important;
    -moz-box-sizing: border-box!important;
    border: 1px solid #BEBEBE!important;
    padding: 1.5rem!important;
    margin: 0px!important;
    -webkit-transition: all 0.30s ease-in-out!important;
    -moz-transition: all 0.30s ease-in-out!important;
    -ms-transition: all 0.30s ease-in-out!important;
    -o-transition: all 0.30s ease-in-out!important;
    transition: all 0.30s ease-in-out!important;
    outline: none!important;
    width: 100%!important;
    height: 4rem!important;
    min-height: 200px;
}

.share-modal input:focus,
.share-modal textarea:focus {
    -moz-box-shadow: 0 0 8px #88D5E9!important;
    -webkit-box-shadow: 0 0 8px #88D5E9!important;
    box-shadow: 0 0 8px #88D5E9!important;
    border: 1px solid #88D5E9!important;
}

.share-modal .row {
    margin-top: 1rem;
}