.user-report-modal .header {
    font-size: 16px;
    padding-bottom: 10px;
    border-bottom: 1px solid #e9e9e9;
}

.user-report-modal .body {
    padding-top: 15px;
}

.user-report-modal .body textarea {
    display: block;
    width: 100%;
    padding: 0.375rem 0.75rem;
    font-size: 1.4rem;
    line-height: 1.5;
    min-height: 10rem;
    color: #495057;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    border-radius: 0.25rem;
    transition: border-color .15s ease-in-out, box-shadow .15s ease-in-out;
    overflow: auto;
    resize: vertical;
}

.subject-ERROR .rodal-dialog,
.subject-UNAVAILABLE .rodal-dialog {
    height: 240px!important;
}

.subject-REPORT .rodal-dialog {
    height: 300px!important;
}

.user-report-modal .rodal-cancel-btn,
.user-report-modal .rodal-confirm-btn {
    position: absolute;
    font: inherit;
    bottom: 1rem;
    padding: 0.5rem;
    border-radius: 3rem;
    height: 4rem;
    width: 10rem;
    transition: background 0.2s;
    border: 1px solid var(--color__100grammes);
}

.user-report-modal .rodal-confirm-btn {
    color: #fff;
    right: 20px;
    background: var(--color__100grammes);
    cursor: pointer;
}

.user-report-modal .rodal-confirm-btn:hover {
    background: var(--color__100grammes);
}

.user-report-modal .rodal-cancel-btn {
    color: var(--color__100grammes);
    right: 16px;
    background: #fff;
    cursor: pointer;
}

.user-report-modal .rodal-cancel-btn:hover {
    background: #fafafa;
}

.user-report-modal .rodal-cancel-btn:focus,
.user-report-modal .rodal-confirm-btn:focus {
    outline: none;
}

.user-report-modal .rodal-confirm-btn:active {
    background: var(--color__100grammes);
}

.user-report-modal .rodal-cancel-btn:active {
    background: #fafafa;
    box-shadow: inset 1px 1px 1px rgba(0, 0, 0, 0.2), 0 0 1px transparent;
}